import style from "./MyPosts.module.css";
import React from "react";
import Post from "./Post/Post";
import {Field, Form, Formik} from "formik";


const MyPosts = React.memo(props => {


    let postsElements = props.posts.map((post, id) => <Post key={id} message={post.message}
                                                            likesCount={post.likesCount}/>).reverse();

    // let newPostElement = createRef();
    //
    // let onAddPost = () => {
    //     props.addPost();
    //     //  BLL(redux)
    //     // props.dispatch(addPostActionCreator());
    //     debugger
    // }
    // //        input(newPostElement)
    // let onPostChange = () => {
    //     let text = newPostElement.current.value;
    //     //  BLL(redux)newText:text    store.js action.newText
    //     //      
    //     // let action = (updateNewPostTextActionCreator(text));
    //     // props.dispatch(action);
    //     props.updateNewPostText(text)
    // }

    const AddMassageForm = (props) => {

        let addNewMessage = (values) => {

            props.addPost(values);
        }

        return (
            <Formik
                initialValues={{
                    newPostText: ""
                }}
                onSubmit={(values, {resetForm}) => {
                    addNewMessage(values.newPostText);
                    resetForm({values: ''});
                }
                }
            >
                {() => (
                    <Form>
                        <div>
                            <Field
                                name={'newPostText'}
                                as={'textarea'}
                                placeholder={' '}
                            />
                        </div>
                        <button type={'submit'}>Send</button>
                    </Form>
                )}
            </Formik>
        )
    }


    return (<div className={style.blockPosts}>

        <h2>My posts</h2>
        <div>
            <AddMassageForm addPost={props.addPost}/>
            {/*<form>*/}
            {/*    <input onChange={onPostChange} value={props.newPostText}*/}
            {/*           type="text" ref={newPostElement} placeholder={" "}/>*/}
            {/*    <button type={"button"} onClick={onAddPost}>Add post</button>*/}
            {/*</form>*/}
            <div className={style.posts}>
                {postsElements}
            </div>
        </div>
    </div>)
})


export default MyPosts;